/* finlandica-regular - latin-ext_latin_cyrillic-ext_cyrillic */
@font-face {
	font-display: swap;
	font-family: 'Finlandica';
	font-style: normal;
	font-weight: 400;
	src: url('../fonts/finlandica-v3-latin-ext_latin_cyrillic-ext_cyrillic-regular.eot'); /* IE9 Compat Modes */
	src: local(''),
			url('../fonts/finlandica-v3-latin-ext_latin_cyrillic-ext_cyrillic-regular.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
			url('../fonts/finlandica-v3-latin-ext_latin_cyrillic-ext_cyrillic-regular.woff2') format('woff2'), /* Super Modern Browsers */
			url('../fonts/finlandica-v3-latin-ext_latin_cyrillic-ext_cyrillic-regular.woff') format('woff'), /* Modern Browsers */
			url('../fonts/finlandica-v3-latin-ext_latin_cyrillic-ext_cyrillic-regular.ttf') format('truetype'), /* Safari, Android, iOS */
			url('../fonts/finlandica-v3-latin-ext_latin_cyrillic-ext_cyrillic-regular.svg#Finlandica') format('svg'); /* Legacy iOS */
}

